.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 10px;
    max-width: 100%;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
}

.friendImageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
}

.friendText {
    background-color: #dd00ff;
    color: white;
    padding: 10px;
    border-radius: 10px;
    position: relative;
    max-width: 80%;
}

.friendText:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 10px 10px;
    border-color: transparent transparent #007bff transparent;
}

.friendName {
    font-weight: bold;
    margin-bottom: 5px;
}

.friendMessageText {
    white-space: pre-wrap;
    word-wrap: break-word;
}

.friendTime {
    align-self: flex-start;
    font-size: 12px;
    color: #888;
    margin-top: 5px;
}
