.spanBlock {
    display: flex;
    align-items: center;
    gap: 10px; /* Расстояние между иконкой и текстом */
    cursor: pointer;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
    transition: background-color 0.3s ease;
}

.span {
    font-size: 16px;
    color: #333;
}

.input {
    font-size: 16px;
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    width: 100%;
    box-sizing: border-box;
    transition: border-color 0.3s ease;
}

.pen {
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: transform 0.3s ease;
}