/* SuperInputText.module.css */

.input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    transition: border-color 0.3s, box-shadow 0.3s;
}

.input:focus {
    border-color: #007bff;
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.inputWrapper {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.superInput {
    /* Дополнительные стили для супер инпута, если нужно */
}

.errorInput {
    border-color: #dc3545;
}

.errorInput:focus {
    border-color: #dc3545;
    outline: none;
    box-shadow: 0 0 5px rgba(220, 53, 69, 0.5);
}

.error {
    color: #dc3545;
    font-size: 14px;
    margin-top: 5px;
}