/* SuperButton.module.css */

.button {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

.disabled {
    background-color: #ccc;
    color: #888;
    cursor: not-allowed;
}

.secondary {
    background-color: #6c757d;
    color: white;
}

.default {
    background-color: #007bff;
    color: white;
}

.red {
    background-color: #dc3545;
    color: white;
}

.default:hover {
    background-color: #0056b3;
}

.red:hover {
    background-color: #c82333;
}

.secondary:hover {
    background-color: #5a6268;
}

.default:active {
    background-color: #004080;
}

.secondary:active {
    background-color: #4e555b;
}

.red:active {
    background-color: #bd2130;
}

.button:disabled {
    background-color: #ccc;
    color: #888;
    cursor: not-allowed;
}