.message {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
    max-width: 100%;
}

.imageAndText {
    display: flex;
    align-items: flex-end;
}

.imageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 10px;
}

.text {
    background-color: #007bff;
    color: white;
    padding: 10px;
    border-radius: 10px;
    position: relative;
    max-width: 80%;
}

.text::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: -10px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 10px 10px;
    border-color: transparent transparent #007bff transparent;
}

.name {
    font-weight: bold;
    margin-bottom: 5px;
}

.messageText {
    white-space: pre-wrap;
    word-wrap: break-word;
}

.time {
    align-self: flex-end;
    font-size: 12px;
    color: #888;
    margin-top: 5px;
}