/* Sidebar.module.css */

.sidebar {
    position: absolute;
    top: 0;
    left: -300px; /* Скрываем сайдбар за левой границей */
    width: 300px;
    height: 100%;
    background-color: #fff;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.2);
    transition: left 0.3s ease;
}

.sidebar.open {
    left: 0; /* Показываем сайдбар */
}

.background {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.nav {
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.nav a {
    text-decoration: none;
    color: #333;
    font-size: 18px;
    padding: 10px 0;
    transition: color 0.3s ease;
}

.nav a:hover {
    color: #007bff;
}

.nav a.active {
    color: #007bff;
    font-weight: bold;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 24px;
    color: #333;
    transition: color 0.3s ease;
}

.close:hover {
    color: #007bff;
}

.close img {
    width: 24px;
    height: 24px;
}